import Vue from 'vue'
import Router from 'vue-router'
import https from '../https.js'
import "babel-polyfill"
Vue.prototype.jsonUrl = '/htftsolu';
Vue.use(Router);
var routes = []; // 三方调用
// 定义路由的时候如下定义，name也可为中文
var obj = {};
var paramData = JSON.parse(getUrlParam("param")) // 取值方式
Vue.prototype.allParam = paramData || {}
var bbDm = paramData.bbDmVersion
obj.path='/';
obj.props={routerParam:paramData}; // 初始化传递参数
obj.component=resolve => require(['@/components/taxTable/'+bbDm.split("_")[0]+'/'+bbDm], resolve);
obj.name=bbDm;
routes.push(obj);
//获取地址栏参数
function getUrlParam (name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
  var r = window.location.search.substr(1).match(reg);
  if (r != null) return decodeURIComponent(r[2]); return null;
}
console.log(routes)
var router = new Router({
  //配置路由
  routes: routes,
})
export default router



