// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import $ from 'jquery'
import App from './App'
import QS from 'qs'
import axios from './axios';//通过import引入
import taxTable from '../static/css/taxTable.css'
import tools from '../static/js/tools.js'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import jqprint from '../static/js/jquery.jqprint-0.3.js'
import jqmigrate from '../static/js/jquery-migrate-1.2.1.min.js'
import https from './https.js'
import "babel-polyfill"
import htmlToPdf from './htmlToPdf/htmlToPdf.js'
import modal from './components/dialog'
Vue.prototype.$axios = axios;
Vue.prototype.qs = QS;
Vue.prototype.$https = https;
Vue.prototype.$elementUi = ElementUI;
Vue.config.productionTip = false
Vue.use(tools) //注册全局方法
Vue.use(jqprint) //注册全局方法
Vue.use(jqmigrate) //注册全局方法
Vue.use(ElementUI) // 样式表
Vue.use(https) // 封装请求方法
Vue.use(htmlToPdf)
Vue.prototype.$EventBus = new Vue() // 用于组件之间传递参数
require('es6-promise').polyfill();
require('es6-promise/auto');
/* eslint-disable no-new */

new Vue({
  el: '#app',
  tools,
  axios,
  router,
  components: { App },
  template: '<App/>'
})



