// 导出页面为PDF格式
import html2Canvas from 'html2canvas'
import https from '../https.js'
import router from '../router/index.js'
/*import "/static/js/html2canvas"*/
import JsPDF from 'jspdf'
import JSZip from 'jszip'
import fileSaver from 'file-saver'

export default{
  install (Vue, options) {
    Vue.prototype.getPdf = function (htmlTitle,isDownloadBranch) {
    // var content =  document.querySelector('#pdfDom');
//克隆一个table（因为显示样式与导出样式不一致，所有用此方法解决）
/*      if(isDownloadBranch){
        var cloneDom = document.getElementById('show-iframe').contentWindow.document.getElementById('pdfDom').clone(true);
      }else{
        var cloneDom = $("#pdfDom").clone(true);
      }*/
      var cloneDom = $("#pdfDom").clone(true);
//排版，不显示在页面上（不能用display:none，会导致无法生成url）
     cloneDom.css({ "position": "absolute", "top": "0px", "z-index": "-1000","padding":"50px"});
//设置id（用于销毁，否则会导致页面重复生成table）
      cloneDom.attr("id", "tmp_datatable");
      $("#allTable").append(cloneDom);
      $("#tmp_datatable button").css('display','none');
      $("#tmp_datatable td").removeClass("editable-bgColor uneditable-bgColor font-color-red")
      $("#tmp_datatable select").removeClass("select-bgcolor")
      $("#tmp_datatable input").css('background-color','#fff')
      $("#tmp_datatable table tr").prepend('<td style="border-width: 0px"></td>');
      var title = htmlTitle;
      html2Canvas(document.querySelector('#tmp_datatable'), {
        allowTaint: true
      }).then(function (canvas) {
       // $("body").append(canvas);
        var contentWidth = canvas.width
        var contentHeight = canvas.height
        var pageHeight = contentWidth / 592.28 * 841.89
        var leftHeight = contentHeight
        var position = 0
        var imgWidth = 595.28
        var imgHeight = 592.28 / contentWidth * contentHeight
        var pageData = canvas.toDataURL('image/jpeg', 1.0)
        console.log(pageData)
          var PDF = new JsPDF('', 'pt', 'a4');
        //销毁克隆对象
          cloneDom.empty();
          $("#tmp_datatable").remove();
          if (leftHeight < pageHeight) {
            PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
          } else {
            while (leftHeight > 0) {
              PDF.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
              leftHeight -= pageHeight
              position -= 841.89
              if (leftHeight > 0) {
                PDF.addPage()
              }
            }
          }
/*            var zip = new JSZip();
            // 向zip文件中添加图片，可以添加多个文件或者图片，此处以图片为例
            // base64图片需要去掉base64图片标识
            var datauri = PDF.output('dataurlstring');
            var base64 = datauri.substring(28);
            console.log(base64)
            zip.file("test.pdf", base64.substring(base64.indexOf(",") + 1), {base64: true});
            zip.file("test1.pdf", base64.substring(base64.indexOf(",") + 1), {base64: true});
            zip.generateAsync({
              type: "blob",  // 压缩类型
              compression: "DEFLATE",      // STORE：默认不压缩 DEFLATE：需要压缩
              compressionOptions: {
                level: 9  // 压缩等级1~9    1压缩速度最快，9最优压缩方式
                // [使用一张图片测试之后1和9压缩的力度不大，相差100字节左右]
              }
            })
              .then(function (content) {
                // 压缩的结果为blob类型（二进制流）,可用作文件上传
                console.info(content);
                // 直接在浏览器打成car.zip包并下载，saveAs依赖的js是FileSaver.js
                fileSaver.saveAs(content, "test.zip");
              });*/
          PDF.save(title + '.pdf')
       //   $('#show-iframe').attr('src', 'http://localhost:8080/htftsolu#/10401_001_12?type=branch');
        }
      )
    }
  }
}




